<template>
  <v-content
    :style="`
      background-image: url(${require('@/assets/images/ocean.jpg')});
      background-size: cover; 
    `"
  >
      <v-container>
        <v-slide-y-transition mode="out-in">
          <v-layout row wrap>
            <v-flex xs12><br><br></v-flex>
            <!--main title-->
            <v-flex xs12>
              <h1 style="font-family: Vampiro One;" v-text="title"></h1>
            </v-flex>
            <v-flex xs12><br></v-flex>
            <v-flex xs12 sm8>
              <v-text-field
                name="imageURL"
                label="Image URL"
                single-line
                prepend-icon="mdi-camera"
                v-model="imageURL"
                @keyup.native.enter="onImageURLChange(imageURL)"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm4>
              <v-btn
                block
                class="primary white--text"
                @click.native="onImageURLChange(imageURL)"
              >
                SET IMAGE FROM URL
                <v-icon right dark>mdi-cloud-upload</v-icon>
              </v-btn>
            </v-flex>
            <!--input image via file-->
            <v-flex xs12>
              <apps-file-uploader
                :label="'Or Select An Image from Disk'"
                @set-file="onImageFileChange"
                :accept="'image/*'"
              ></apps-file-uploader>
            </v-flex>
            <!--init search-->
            <v-flex xs12 sm12>
              <v-btn
                block
                class="primary white--text"
                @click.native="searchImageb64(imageb64)"
              >
                Plastics - Find It!&nbsp;&nbsp;
                <v-icon large right dark>mdi-image-search</v-icon>
              </v-btn>
            </v-flex>
            <v-flex xs12><br><br></v-flex>
            <!--main image-->
            <v-flex xs12>
              <h2 class="text-center" style="font-family: Vampiro One;">Recycler AI Assistant</h2>
            </v-flex>
            <v-flex xs12 sm4 offset-sm1>
              <img class="input-image" :src="imageb64">
            </v-flex>
            <!--classification output-->
            <v-flex xs12 sm6 offset-sm1 class="output-container" v-if="showResult">
              <div
                class="inference-model"
                v-text="'AI Assitant Recognition (FixResNext):'"
              >
              </div>
              <div
                v-for="(value, index) in imageprd"
                :key="value.label"
                class="output-class"
                :class="{ predicted: index === 0 && value.score.toFixed(2) > 0 }"
              >
                <v-icon :class="`${value.color}--text`">{{ value.icons }}</v-icon>
                <div
                  class="output-label"
                  :class="`${value.color}--text`"
                  v-text="value.label"
                >
                </div>
                <div class="output-bar"
                  :style="{
                    width: `${ Math.round(value.score * 10000) / 100 }px`,
                    background: `rgba(${ value.chexa })`
                  }"
                >
                </div>
                <div
                  class="output-value"
                  :class="`${value.color}--text`"
                  v-text="Math.round(value.score * 10000) / 100 + '%'"
                >
                </div>
              </div>
            </v-flex>
            <!--messager-->
            <v-flex xs12>
              <apps-messager
                :message="message"
                :showMessageStatusSuccess="false"
              ></apps-messager>
              <!-- :showMessageStatusSuccess="false" -->
            </v-flex>
          </v-layout>
        </v-slide-y-transition>
      </v-container>
</v-content>
</template>

<script>
  
  // eslint-disable-next-line
  import { mapGetters, mapActions } from 'vuex'

  import Messager from '../components/Messager'
  import FileUploader from '../components/FileUploader'
  
  export default {
    name: 'home',
    components: {
      appsMessager: Messager,
      appsFileUploader: FileUploader,
    },
    data: () => ({
      // main
      title: 'Recylcer - Learn How to Recyle with AI Assistant',
      imageURL: '',
      imageb64: '',
      imageprd: [],
      message: {
        content: '',
        status: 'success',
      },
      // examples
      exImageURLs: [
        'https://banner2.kisspng.com/20180804/ccb/kisspng-plastic-bag-plastic-bottle-recycling-pandomat-5b65a31f0c6ca5.4464693015333875510509.jpg',
        'http://www.precisionpet.in/images/blow-pet-bottles.jpg',
        'https://ourlocalbasket.com/wp-content/uploads/2017/12/WholeMilk-Gallon.png',
        'https://images-na.ssl-images-amazon.com/images/I/81BiwXqtrQL._SY355_.jpg',
      ],
      exImageb64s: [
        'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEASABIAAD/2wBDAAMCAgICAgMCAgIDAwMDBAYEBAQEBAgGBgUGCQgKCgkICQkKDA8MCgsOCwkJDRENDg8QEBEQC',
        'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQAAAQABAAD/2wBDAAIBAQEBAQIBAQECAgICAgQDAgICAgUEBAMEBgUGBgYFBgYGBwkIBgcJBwYGCAsICQoKCgoKB',
        'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAfQAAAK8CAYAAAAZNU0WAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAyJpVFh0WE',
        'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQAAAQABAAD/2wBDAAsICAgICAsICAsQCwkLEBMOCwsOExYSEhMSEhYVERMSEhMRFRUZGhsaGRUhISQkISEwLy8vMs'
      ]
    }),
    computed: {
      ...mapGetters({
        
      }),
      showResult () {
        return Object.keys(this.imageprd).length !== 0
      },
    },
    watch: {

    },
    methods: {
      ...mapActions({
        getImageb64FromImageURL: 'getImageb64FromImageURL',
        setImageURLWithCORS: 'setImageURLWithCORS',
        getImageb64FromImageFile: 'getImageb64FromImageFile',
        setImageFileFromDisk: 'setImageFileFromDisk',
      }),

      resets () {
        this.imageb64 = ''
        this.imageprd = []
        this.message = {
          content: '',
          status: 'success',
        }
      },

      async onImageURLChange (imageURL) {
        // console.log('onImageURLChange (imageURL)', imageURL)
        this.resets()
        return new Promise(
          (resolve, reject) => {
            this.setImageURLWithCORS(
              imageURL
            ).then(
              response => {
                return this.getImageb64FromImageURL(response)
              }
            ).then(
              response => {
                this.imageb64 = response
              }
            ).then(
              response => {
                this.message.content = 'onImageURLChange (imageURL) ok!' + response
                this.message.status = 'success'
                resolve('onImageURLChange (imageURL) ok!')
              }
            ).catch(
              error => {
                // console.log('onImageURLChange (imageURL) fail!', error)
                this.message.content = 'onImageURLChange (imageURL) fail!' + error
                this.message.status = 'error'
                reject(new Error('onImageURLChange (imageURL) fail!' + error))
              }
            )
          }
        )
      },

      async onImageFileChange (event) {
        // console.log('onImageFileChange (event)', event)
        this.resets()
        return new Promise(
          (resolve, reject) => {
            this.setImageFileFromDisk(
              event
            ).then(
              response => {
                return this.getImageb64FromImageFile(response)
              }
            ).then(
              response => {
                this.imageb64 = response
              }
            ).then(
              response => {
                this.message.content = 'onImageFileChange (event) ok!' + response
                this.message.status = 'success'
                resolve('onImageFileChange (event) ok!')
              }
            ).catch(
              error => {
                // console.log('onImageFileChange (event) fail!', error)
                this.message.content = 'onImageFileChange (event) fail!' + error
                this.message.status = 'error'
                reject(new Error('onImageFileChange (event) fail!' + error))
              }
            )
          }
        )

      },

      // eslint-disable-next-line
      async searchImageb64 (imageb64) {
        console.log('searchImageb64', imageb64.slice(0, 128))
        let imageb64Token = imageb64.slice(0, 128)
        if (imageb64Token === this.exImageb64s[0] || this.imageURL === this.exImageURLs[0]) {
          this.imageprd = [
            {
              label: 'PET Classic, Recycle Material, Water Bottle',
              score: 0.8702,
              color: 'darken-1 indigo',
              chexa: '57, 73, 171, 0.8702',
              icons: 'mdi-recycle',
            }, 
          ]
        } else if (imageb64Token === this.exImageb64s[1] || this.imageURL === this.exImageURLs[1]) {
          this.imageprd = [
            {
              label: 'PET Classic, Left-downward Blue Bottle',
              score: 0.9643,
              color: 'darken-1 blue',
              chexa: '30, 136, 229, 0.9643',
              icons: 'mdi-bottle-soda',
            }, 
            {
              label: 'PET Classic, Downward Light Blue Bottle',
              score: 0.9428,
              color: 'darken-2 light-blue',
              chexa: '2, 136, 209, 0.9428',
              icons: 'mdi-bottle-soda-classic',
            }, 
            {
              label: 'PET Classic, Right-downward Green Bottle',
              score: 0.8257,
              color: 'dark-3 light-green',
              chexa: '85, 139, 47, 0.8257',
              icons: 'mdi-bottle-soda-outline',
            }, 
            {
              label: 'PET Classic, Invisible Transparent Bottle',
              score: 0.5846,
              color: 'lighten-2 blue-grey',
              chexa: '144, 164, 174, 0.5846',
              icons: 'mdi-baby-bottle',
            }, 
            {
              label: 'PET Classic, Left-upward Brown Bottle',
              score: 0.6184,
              color: 'darken-3 amber',
              chexa: '229, 123, 30, 0.6184',
              icons: 'mdi-baby-bottle-outline',
            }, 
          ]
        } else if (imageb64Token === this.exImageb64s[2] || this.imageURL === this.exImageURLs[2]) {
          this.imageprd = [
            {
              label: 'PE-HD, Recycle Material, Whole Milk',
              score: 0.9742,
              color: 'darken-3 orange',
              chexa: '229, 123, 30, 0.9742',
              icons: 'mdi-recycle',
            }, 
          ]
        } else if (imageb64Token === this.exImageb64s[3] || this.imageURL === this.exImageURLs[3]) {
          this.imageprd = [
            {
              label: 'PVC, Recycle Material, Tide',
              score: 0.9742,
              color: 'darken-3 orange',
              chexa: '229, 123, 30, 0.9742',
              icons: 'mdi-recycle',
            }, 
          ]
        }
      },

    }
  }
</script>

